@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#pollform {
    background-color: rgb(255, 254, 235);
}

#question-field {
    width: 100%;
    font-size: 20px;
    font-family: 'Sriracha', cursive;
    background-color: rgb(255, 254, 235);
    border: none;
    border-bottom: 1px solid black;
    line-height: 50px;
    margin-bottom: 40pt;
}

#input-field {
    width: 100%;
    font-size: 20px;
    font-family: 'Sriracha', cursive;
    background-color: rgb(255, 254, 235);
    border: none;
    border-bottom: 1px solid black;
    line-height: 50px;
}

#form-button {
    font-family: 'Calibri';
    font-size: 20px;
    padding: 20px;
    padding-left: 20px;
    margin-top: 40pt;
    margin-bottom: 20pt;
    background-color: rgb(255, 200, 61);
    border-radius: 30px;
    border: 1px solid rgb(180, 161, 55);
}

#form-button:hover {
    background-color: rgb(255, 235, 123);
}
#main-header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(40, 40, 40);
}

#main-header-txt {
    color: rgb(255, 254, 235);
    font-family: 'Calibri Light';
    font-size: 20px;
}

.main {
    display: flex;
    min-height: 74.4vh;
    flex: 1 1;
    background-color: rgb(40, 40, 40);
    justify-content: center;
}

#pollmaker-background {
    flex: 0 1 60vh;
    margin-top: 80pt;
    margin-bottom: 80pt;
    padding-left: 5%;
    padding-right: 5%;
}

#pollmaker {
    width: 100;
    padding-left: 12%;
    padding-right: 12%;
    padding-top: 10%;
    background-color: rgb(255, 254, 235);
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.500);
}


.Header {
    height: 100px;
    background-color: rgb(40, 40, 40);
    display: flex;
}

#header {
    display: flex;
    flex: 1 1;
    height: 60px;
    background-color: rgb(255, 254, 235);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.500);
    justify-content: flex-end;
}

#header-buttons {
    width: 300px;
}

#header-button {
    font-size: 20px;
    font-family: 'Calibri';
    background-color: rgb(255, 254, 235);
    border: none;
    height: 60px;
    width: 150px;
    transition: 0.3s;
}

#header-button:hover {
    background-color: rgb(255, 200, 61);
}
.poll-directory {
    display: flex;
    flex: 1 1;
    min-height: 92.2vh;
    background-color: rgb(40, 40, 40);
    justify-content: center;
    align-content: center;
}

#poll-dir-table{
    flex: 0 1 60vh;
    margin-top: 80pt;
    margin-bottom: 80pt;
    padding-left: 5%;
    padding-right: 5%;
}

#dir-header {
    color: rgb(255, 254, 235);
    font-family: 'Permanent Marker', cursive;
    font-size: 30px;
}

.dir-table {
    font-size: 20px;
    font-family: 'Sriracha', cursive;
    background-color: rgb(255, 254, 235);
    border: 2px solid rgb(255, 254, 235);
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.500);
    border-collapse: collapse;
}

.dir-table tr{
    border-bottom: 1px solid black;
    line-height: 40px;
}

.dir-table tbody td:first-child{
    width: 100%;
    overflow-wrap: break-word;
    padding: 5%;
}

.dir-table tbody td:nth-child(2){
    width: 20%;
    margin-right: 10%;
    align-items: center;
    padding-right: 5%;
}

.del-button{
    font-family: 'Calibri';
    background-color: rgb(255, 225, 56);
    border-radius: 30px;
    border: none;
    border: 1px solid rgb(180, 161, 55);
    margin-right: 15px;
}

.del-button:hover{
    background-color: rgb(255, 235, 123);
}

#no-polls {
    color: rgb(255, 254, 235);
    font-family: 'Sriracha', cursive;
    font-size: 20px;
}
.poll {
    display: flex;
    background-color: rgb(40, 40, 40);
    min-height: 92.2vh;
    justify-content: center;
    align-content: center;
}

#voteform {
    flex: 0 1 60vh;
    margin-top: 80pt;
    margin-bottom: 80pt;
    padding-left: 5%;
    padding-right: 5%;
}

#poll-table {
    width: 100%;
}

#poll-header {
    color: rgb(255, 254, 235);
    font-family: 'Sriracha', cursive;
}

.poll-table{
    font-size: 20px;
    font-family: 'Sriracha', cursive;
    background-color: rgb(255, 254, 235);
    border: 2px solid rgb(255, 254, 235);
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.500);
    border-collapse: collapse;
}

.poll-table tr{
    border-bottom: 1px solid black;
    line-height: 40px;
}

.poll-table tbody td:first-child{
    width: 100%;
    overflow-wrap: break-word;
    padding: 5%;
}

.poll-table tbody td:nth-child(2){
    width: 20%;
    margin-right: 10%;
    align-items: center;
    padding-right: 5%;
}

.choice-button {
    margin-right: 15px;
}

#poll-button-container{
    display: flex;
    justify-content: space-between;
}

.answer-button {
    font-size: 20px;
    font-family: 'Calibri';
    padding: 20px;
    padding-left: 20px;
    background-color: rgb(255, 200, 61);
    border-radius: 30px;
    border: 1px solid rgb(180, 161, 55);
}

.result-button {
    font-size: 20px;
    font-family: 'Calibri';
    text-indent: 18px;
    padding: 20px;
    padding-left: 10px;
    padding-right: 32px;
    background-color: rgb(143, 148, 160);
    border-radius: 30px;
    border: 1px solid rgb(100, 104, 112);
    align-self: flex-end;
}

.answer-button:hover {
    background-color: rgb(255, 235, 123);
}

.result-button:hover {
    background-color: rgb(164, 169, 182);
}
@-webkit-keyframes slideInFromLeft {
    0% {
      width: 0%;
    }
    100% {
      width: width;
    }
}

@keyframes slideInFromLeft {
    0% {
      width: 0%;
    }
    100% {
      width: width;
    }
}

.result-bar {
    position: relative;
    height: 20px;
    width: 100%;
    border-radius: 50px;
    border: 2px solid #000000
}

.filler {
    background: rgb(255, 200, 61);
    height: 100%;
    border-radius: inherit;
    -webkit-animation: .2s ease-out 0s 1 slideInFromLeft;
            animation: .2s ease-out 0s 1 slideInFromLeft;
}
.split-para      { display:block;margin:10px;}
.split-para span { display:block;float:right;width:20%;margin-left:10px;}

.poll-results {
    display: flex;
    background-color: rgb(40, 40, 40);
    min-height: 92.2vh;
    justify-content: center;
    align-content: center;
}

#results-header {
    font-family: 'Sriracha', cursive;
    color: rgb(255, 254, 235);
}

#results-table {
    flex: 0 1 60vh;
    margin-top: 80pt;
    margin-bottom: 80pt;
    padding-left: 5%;
    padding-right: 5%;
}

#results {
    background-color: rgb(255, 254, 235);
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.500);
    padding: 5%;
    padding-bottom: 10%;
}

#results tr {
    line-height: 20px;
}

#results td:first-child {
    width: 100%;
    font-family: 'Sriracha', cursive;
    text-align: justify;
}

#answer-count {
    font-family: 'Calibri';
}

#results td:nth-child(2) {
    width: 20%;
    font-family: 'Calibri';
    margin-right: 10%;
    padding-left: 20px;
    padding-bottom: 5px;
    vertical-align: bottom;
}
